function sliderSpecialists() {
    var swiper = new Swiper('.specialists .swiper', {
        slidesPerView: "3",
        spaceBetween: 25,
        navigation: {
            nextEl: '.specialists .swiper-button-next',
            prevEl: '.specialists .swiper-button-prev',
        },
        breakpoints: {
            1150: {
                slidesPerView: "3",
            },
            992: {
                slidesPerView: "2",
            },
            850: {
                slidesPerView: "2",
            },
            320: {
                slidesPerView: "1",
            }
        }
    })
}

function sliderReviews() {
    var swiper = new Swiper('.reviews .swiper', {
        slidesPerView: "auto",
        spaceBetween: 120,
        centeredSlides: true,
        initialSlide: 1,
        loop: true,
        breakpoints: {
            1500: {
                spaceBetween: 120,
            },
            1150: {
                spaceBetween: 60,
            },
            992: {
                spaceBetween: 60,
            },
            320: {
                spaceBetween: 30,
            }
        }
    })
}

$(document).ready(function() {
  sliderSpecialists();
  sliderReviews();

  // Info
    $('.info__card-btn').hover(function () {
        $(this).parent('.info__card').toggleClass('info__card--active')
    })

  // Like
  $('.news__like-btn').click(function () {
    $(this).toggleClass('news__like-btn--active')
  })

    // Tabs
    $(".contacts__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".contacts__tab").removeClass("contacts__tab--active")
        $(this).addClass("contacts__tab--active")
        $('.contacts__map').toggleClass('contacts__map--active')

        $(this).parents(".contacts__left-wrap").find(".contacts__content").removeClass("contacts__content--active")
        $(this).parents(".contacts__left-wrap").find(`.contacts__content[data-content-path="${path}"]`).addClass("contacts__content--active")
    })
})